<template>
  <div class="newsinfo-container">
    <!--  -->
    <h3 class="title">{{ newsinfo.title }}</h3>
    <!--  -->
    <p class="subtitle">
      <span>{{ newsinfo.add_time | dateFormat }}</span>
      <span>{{ newsinfo.click }}</span>
    </p>

    <hr>

    <!--  -->
    <div class="content" v-html="newsinfo.content"></div>

    <!-- 3.  -->
    <comment-box :id="this.id"></comment-box>
  </div>
</template>

<script>
// 1.  
import comment from '@/common/comment.vue'
import { Toast } from 'mint-ui'
import axios from 'axios'

export default {
  data () {
    return {
      id: this.$route.params.id, //  URL  Id data
      newsinfo: {} // 
    }
  },
  created () {
    this.getNewsInfo()
  },
  methods: {
    getNewsInfo () {
      // 
      axios.get('http://www.liulongbin.top:3005/api/getnew/' + this.id).then(result => {
        if (result.data.status === 0) {
          this.newsinfo = result.data.message[0]
        } else {
          Toast('')
        }
      })
    }
  },
  components: {
    // 
    'comment-box': comment
  }
}
</script>

<style lang="scss">
// 
// scoped
.newsinfo-container {
  padding: 0 4px;
  .title {
    font-size: 16px;
    text-align: center;
    margin: 15px 0;
    color: red;
  }
  .subtitle {
    font-size: 13px;
    color: #226aff;
    display: flex;
    justify-content: space-between;
  }
  .content {
    img {
      width: 100%;
    }
  }
}
</style>



