import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

//    main.js,   store 
var localcar = JSON.parse(localStorage.getItem('car') || '[]')
// new Vuex.Store()  
export default new Vuex.Store({
  state: {
    //  state   data ,
    //  store  this.$store.state.*** 
    car: localcar //   car 
    // { id:id, count: , price: selected: false  }
  },
  mutations: {
    //   store  state   mutations  state  
    //   mutations  this.$store.commit('')
    //   mutations  this.$emit('')
    //  mutations  1  state  2  commit 
    addToCar (state, goodsinfo) {
      //  store  car 
      // 
      // 1. 
      // 2.  push  car 

      //  
      var flag = false
      console.log(goodsinfo.count)
      state.car.some(item => {
        // 1. 
        if (item.id === goodsinfo.id) {
          console.log(item.count, goodsinfo.count)
          item.count += parseInt(goodsinfo.count)
          flag = true
          return true
        }
      })

      //  flag  false push  
      if (!flag) {
        state.car.push(goodsinfo)
      }

      //   car  car   localStorage 
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    updateGoodsInfo (state, goodsinfo) {
      // 
      // 
      state.car.some(item => {
        if (item.id === goodsinfo.id) {
          item.count = parseInt(goodsinfo.count)
          return true
        }
      })
      //  
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    removeFormCar (state, id) {
      // Idstore 
      state.car.some((item, i) => {
        if (item.id === id) {
          state.car.splice(i, 1)
          return true
        }
      })
      //  
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    updateGoodsSelected (state, info) {
      state.car.some(item => {
        if (item.id === info.id) {
          item.selected = info.selected
        }
      })
      //   store 
      localStorage.setItem('car', JSON.stringify(state.car))
    }
  },
  getters: { // this.$store.getters.***
    //  getters    state   mutations
    //  getters    getters   
    //  getters  computed   state  getters   getters 
    // 
    getAllCount (state) {
      var c = 0
      state.car.forEach(item => {
        c += item.count
      })
      return c
    },
    //  { 88: 2, 89: 1, 90: 4 }
    getGoodsCount (state) {
      var o = {}
      state.car.forEach(item => {
        o[item.id] = item.count
      })
      return o
    },
    //  { 88: true, 89: false, 90: true }
    getGoodsSelected (state) {
      var o = {}
      state.car.forEach(item => {
        o[item.id] = item.selected
      })
      return o
    },
    // 
    getGoodsCountAndAmount (state) {
      var o = {
        count: 0, // 
        amount: 0 // 
      }
      state.car.forEach(item => {
        if (item.selected) {
          o.count += item.count
          o.amount += item.price * item.count
        }
      })
      return o
    }
  }
})
// 
// 1. state mutations
// 2.   state   this.$store.state.***
// 3.   mutations  this.$store.commit('' )
// 4.  store  state    getters,  getters , this.$store.getters.***



