<template>
  <mt-header fixed title="vue-cli">
    <span slot="left" @click="goBack" v-show="flag">
      <mt-button icon="back"></mt-button>
    </span>
  </mt-header>
</template>

<script>
export default {
  name: 'PubHeader',
  data () {
    return {
      flag: false
    }
  },
  created () {
    // created
    this.flag = !this.$route.path === '/home'
  },
  methods: {
    goBack () {
      // 
      this.$router.go(-1)
    }
  },
  watch: {
    '$route.path': function (newVal) {
      // 
      if (newVal === '/home') {
        this.flag = false
      } else {
        this.flag = true
      }
    }
  }
}
</script>

<style>

</style>



