<template>
  <div class="goods-list">
    <!-- <router-link class="goods-item" v-for="item in goodslist" :key="item.id" :to="'/home/goodsinfo/' + item.id" tag="div">
      <img :src="item.img_url" alt="">
      <h1 class="title">{{ item.title }}</h1>
      <div class="info">
        <p class="price">
          <span class="now">{{ item.sell_price }}</span>
          <span class="old">{{ item.market_price }}</span>
        </p>
        <p class="sell">
          <span></span>
          <span>{{ item.stock_quantity }}</span>
        </p>
      </div>
    </router-link>-->

    <!--  -->
    <!-- 1  a router-link    -->
    <!-- 2  window.location.href   -->
    <div class="goods-item" v-for="item in goodslist" :key="item.id" @click="goDetail(item.id)">
      <img :src="item.img_url" alt>
      <h1 class="title">{{ item.title }}</h1>
      <div class="info">
        <p class="price">
          <span class="now">{{ item.sell_price }}</span>
          <span class="old">{{ item.market_price }}</span>
        </p>
        <p class="sell">
          <span></span>
          <span>{{ item.stock_quantity }}</span>
        </p>
      </div>
    </div>

    <mt-button type="danger" size="large" @click="getMore"></mt-button>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    // data 
    return {
      pageindex: 1, // 
      goodslist: [] // 
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    getGoodsList () {
      // 
      axios.get('http://www.liulongbin.top:3005/api/getgoods?pageindex=' + this.pageindex)
        .then(result => {
          if (result.data.status === 0) {
            // this.goodslist = result.body.message;
            this.goodslist = this.goodslist.concat(result.data.message)
          }
        })
    },
    getMore () {
      this.pageindex++
      this.getGoodsList()
    },
    goDetail (id) {
      // JS

      //   this.$route  this.$router 
      // this.$route  params, query 
      // this.$router JSURL

      console.log(this)
      // 1. 
      // this.$router.push("/home/goodsinfo/" + id);
      // 2. 
      // this.$router.push({ path: "/home/goodsinfo/" + id });
      // 3. router.jsgoodsinfo
      this.$router.push({ name: 'goodsinfo', params: { id } })
    }
  }
}
</script>

<style lang="scss" scoped>
.goods-list {
  display: flex;
  flex-wrap: wrap;
  padding: 7px;
  justify-content: space-between;

  .goods-item {
    width: 49%;
    border: 1px solid #ccc;
    box-shadow: 0 0 8px #ccc;
    margin: 4px 0;
    padding: 2px;
    // 
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // 
    min-height: 293px;
    img {
      width: 100%;
    }
    .title {
      font-size: 14px;
    }

    .info {
      background-color: #eee;
      p {
        margin: 0;
        padding: 5px;
      }
      .price {
        .now {
          color: red;
          font-weight: bold;
          font-size: 16px;
        }
        .old {
          text-decoration: line-through;
          font-size: 12px;
          margin-left: 10px;
        }
      }
      .sell {
        display: flex;
        justify-content: space-between;
        font-size: 13px;
      }
    }
  }
}
</style>



