<template>
  <div>
    <mt-swipe :auto="4000">
      <!-- v-for key -->
      <!--   lunbotuList -->
      <!-- lunbotuList   -->
      <mt-swipe-item v-for="item in lunbotuList" :key="item.img">
        <img :src="item.img" alt="" :class="{'full': isfull}">
      </mt-swipe-item>
    </mt-swipe>
  </div>

  <!--    -->
  <!-- 1.   100%  -->
  <!-- 2.     100% -->
  <!-- 3.   100%    -->
  <!-- 4.       100%   -->
  <!-- 5. 100%  -->

</template>

<script>
export default {
  props: ['lunbotuList', 'isfull']
}
</script>

<style lang="scss" scoped>
.mint-swipe {
  height: 200px;

  .mint-swipe-item {
    text-align: center;

    img {
      // width: 100%;
      height: 100%;
    }
  }
}

.full {
  width: 100%;
}
</style>



