<template>
  <div class="shopcar-container">
    <div class="goods-list">
      <!--  -->
      <div class="mui-card" v-for="(item, i) in goodslist" :key="item.id">
        <div class="mui-card-content">
          <div class="mui-card-content-inner">
            <!--  -->
            <mt-switch
              v-model="$store.getters.getGoodsSelected[item.id]"
              @change="selectedChanged(item.id, $store.getters.getGoodsSelected[item.id])"
            ></mt-switch>
            <img :src="item.thumb_path">
            <div class="info">
              <h1>{{ item.title }}</h1>
              <p>
                <span class="price">{{ item.sell_price }}</span>
                <!--  -->
                <!-- 1.  Idcount  
                 { 88: 2, 89: 1, 90: 4 }-->
                <numbox :initcount="$store.getters.getGoodsCount[item.id]" :goodsid="item.id"></numbox>
                <a href="#" @click.prevent="remove(item.id, i)"></a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner jiesuan">
          <div class="left">
            <p></p>
            <p>
              
              <span class="red">{{ $store.getters.getGoodsCountAndAmount.count }}</span>  
              <span class="red">{{ $store.getters.getGoodsCountAndAmount.amount }}</span>
            </p>
          </div>
          <mt-button type="danger"></mt-button>
        </div>
      </div>
    </div>

    <!-- <p>{{ $store.getters.getGoodsSelected }}</p> -->
  </div>
</template>

<script>
import axios from 'axios'
import numbox from '@/common/shopcar_numbox.vue'

export default {
  data () {
    return {
      goodslist: [] // 
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    getGoodsList () {
      // 1.  store Id  api
      var idArr = []
      this.$store.state.car.forEach(item => idArr.push(item.id))
      //  
      if (idArr.length <= 0) {
        return
      }
      // 
      axios.get('http://www.liulongbin.top:3005/api/goods/getshopcarlist/' + idArr.join(','))
        .then(result => {
          if (result.data.status === 0) {
            this.goodslist = result.data.message
          }
        })
    },
    remove (id, index) {
      // storeIdgoodslist index 
      this.goodslist.splice(index, 1)
      this.$store.commit('removeFormCar', id)
    },
    selectedChanged (id, val) {
      //   store 
      // console.log(id + " --- " + val);
      this.$store.commit('updateGoodsSelected', { id, selected: val })
    }
  },
  components: {
    numbox
  }
}
</script>

<style lang="scss" scoped>
.shopcar-container {
  background-color: #eee;
  overflow: hidden;
  .goods-list {
    .mui-card-content-inner {
      display: flex;
      align-items: center;
    }
    img {
      width: 60px;
    }
    h1 {
      font-size: 13px;
    }
    .info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .price {
        color: red;
        font-weight: bold;
      }
    }
  }
  .jiesuan {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .red {
      color: red;
      font-weight: bold;
      font-size: 16px;
    }
  }
}
</style>



